.info-section {
  @include font-size(16);

  h3 {
    @include font-size(30);
    font-weight: $light;
    margin-bottom: 15px;
  }

  @include media(min, 500) {
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
  }

  @include media(min, 1024) {
    max-width: none;

    .container {
      display: flex;
    }
  }
}

.info-section-item {
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }

  @include media(min, 1024) {
    margin-right: 20px;
    margin-bottom: 0;
    width: 100%;

    &:last-child {
      margin-right: 0;
      max-width: 325px;
    }
  }

  @include media(min, 1224) {
    &:nth-child(2) {
      max-width: 425px;
    }
  }
}

.info-item-wrap {
  padding-top: 15px;
  border-top: 2px solid $main-green;
}

.inner-item {
  margin-top: 26px;
  margin-left: 30px;
  padding: 22px;
  border: 2px solid $main-green;

  p {
    margin-bottom: 10px;
  }

  &.about-us {
    position: relative;

    &:before {
      content: url("../img/about-us-img.png");
      position: absolute;
      top: -31px;
      left: -34px;
      background: #fff;
      height: 35px;
    }

    a {
      position: relative;
      display: inline-block;
      margin-top: 25px;
      padding-right: 25px;
      color: $main-green;

      &:before,
      &:after {
        content: '';
        position: absolute;
      }

      &:before {
        width: 20px;
        height: 1px;
        transform: translateY(-50%);
        top: 50%;
        right: 0;
        background: $main-green;
      }

      &:after {
        position: absolute;
        top: 4px;
        right: 0;
        width: 6px;
        height: 6px;
        transform: rotate(45deg);
        border-top: 1px solid;
        border-right: 1px solid;
      }
    }
  }

  @include media(min, 1224) {
    padding: 20px;

    p {
      margin-bottom: 20px;
    }

    &.about-us {
      a {
        margin-top: 0;

        &:after {
          top: 6px;
        }
      }
    }
  }

  @include media(min, 1600) {
    @include font-size(16);
    padding: 30px;

    p {
      margin-bottom: 25px;
    }

    &.about-us {
      a {
        margin-top: 5px;
      }
    }
  }
}

// Day product
.day-product-item {
  border: 1px solid $light-txt;
  margin-bottom: 18px;
  padding: 15px;

  &:last-child {
    margin-bottom: 0;
  }

  img {
    margin: 0 auto 15px;
    width: 100px;
  }

  @include media(min, 500) {
    display: flex;

    img {
      margin-bottom: 0;
      margin-right: 15px;
    }
  }
  
  @include media(min, 1024) {
    display: block;

    img {
      margin: 0 auto 15px;
    }
  }

  @include media(min, 1224) {
    display: flex;
    align-items: center;

    img {
      margin-bottom: 0;
      margin-right: 15px;
    }
  }

  @include media(min, 1600) {
    img {
      width: auto;
    }
  }
}

.day-product-subtitle,
.good-item-subtitle {
  font-weight: $light;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;

  @include media(min, 1224) {
    margin-top: 15px;
  }

  @include media(min, 1600) {
    @include font-size(12);
  }
}

.delivery-info {
  margin-right: 15px;

  p {
    padding-left: 22px;
    position: relative;
  }
}

.delivery-title {
  &:before {
    content: url("../img/delivery-img.png");
    position: absolute;
    top: 0;
    left: 0;
  }
}

.amount {
  &:before {
    content: '';
    position: absolute;
    top: 2px;
    left: 4px;
    width: 10px;
    height: 6px;
    transform: rotate(135deg);
    border-top: 1px solid $txt-green;
    border-right: 1px solid $txt-green;
  }
}

.delivery-title {
  margin-bottom: 5px;
}

.price-title {
  @include font-size(14);
  margin-bottom: 5px;

  @include media(min, 1600) {
    @include font-size(10);
    margin-bottom: 0;
  }
}

.price {
  @include font-size(20);
  color: $txt-green;
}

// Usefull item
.usefull-item {
  display: flex;
  margin-bottom: 15px;

  &:last-child {
    margin-bottom: 0;
  }
}

.usefull-item-date {
  margin-right: 10px;
  padding: 10px 5px;
  border: 4px solid $main-green;
  text-align: center;
}

.date-day {
  @include font-size(34);
  font-weight: $bold;
  margin-bottom: 5px;
  line-height: 1;
}

.usefull-item-desc {
  @include font-size(14);
  font-weight: $light;
}

.usefull-item-title {
  @include font-size(20);
  margin-bottom: 10px;
  font-weight: $regular;

  @include media(min, 1600) {
    margin-bottom: 0;
  }
}
