.categories-sect {
  margin-bottom: 45px;

  .container {
    padding-bottom: 45px;
    overflow: hidden;
  }

  .slick-slide {
    margin: 0 30px 0 0;
  }

  .slick-prev,
  .slick-next {
    bottom: -40px;

    &:before {
      padding: 0 10px;
      border: 2px solid $main-green;
      color: $main-green;
    }
  }

  .slick-prev {
    left: 0;
  }

  .slick-next {
    right: 34px;
  }

  @include media(min, 768) {
    .container {
      padding-bottom: 0;
      padding-top: 5px;
    }

    .slick-prev,
    .slick-next {
      top: -50px;
    }

    .slick-prev {
      left: auto;
      right: 100px;
    }
  }

  @include media(min, 1600) {
    .slick-slide {
      max-width: 320px;
    }
  }
}

h2 {
  @include font-size(26);
  font-weight: $light;
  position: relative;
  margin-bottom: 30px;

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 100%;
    border-bottom: 2px solid $main-green;
  }

  span {
    padding-right: 10px;
    background: #fff;
  }

  @include media(min, 500) {
    @include font-size(36);
  }

  @include media(min, 768) {
    overflow: hidden;

    &:after {
      right: 130px;
    }

    span {
      position: relative;
      z-index: 1;
    }
  }
}

.best-goods-wrapper {
  display: flex;
}

.good-item {
  position: relative;
  margin-right: 20px;
  padding: 40px 20px 20px;
  min-width: 270px;
  border: 1px solid $light-txt;

  &:first-child {
    margin-left: 6px;
  }

  &:last-child {
    margin-right: 0;
  }

  img {
    display: block;
    margin: 0 auto 30px;
    width: 170px;
  }

  button {
    @include font-size(18);
    margin-right: 20px;
    padding: 8px 0;
    width: 115px;
    border-radius: 5px;
    border: 0;
    background: $btn-color;
    color: $bg-txt;
    box-shadow: 0 0 2px rgba(0, 0, 0, .5);
    text-transform: uppercase;
    outline: none;
  }

  a {
    @include font-size(14);
    color: $light-txt;
    border-bottom: 1px dashed;

    span {
      color: #cc0619;
    }
  }

  @include media(min, 1224) {
    button {
      @include font-size(17);
      margin-right: 15px;
      width: 95px;
    }
  }

  @include media(min, 1600) {
    max-width: 320px;

    button {
      margin-right: 30px;
      padding: 15px 0;
      width: 125px;
    }
  }
}

.product-tip {
  @include font-size(14);
  font-weight: $bold;
  position: absolute;
  top: 14px;
  left: -6px;
  padding: 5px 10px;
  color: $bg-txt;
  text-transform: uppercase;

  &:before,
  &:after {
    content: '';
    position: absolute;
  }

  &:before {
    bottom: -7px;
    left: 0;
    border-top: 7px solid;
    border-left: 5px solid transparent;
  }

  &:after {
    top: 0;
    right: -8px;
    border-top: 11px solid;
    border-bottom: 11px solid;
    border-right: 8px solid transparent;
  }
}

.good-item-title {
  @include font-size(16);
  margin-bottom: 20px;
}

.good-item-subtitle {
  @include font-size(12);
  margin-bottom: 30px;

  .price-title {
    @include font-size(10);
    margin-bottom: 0;
  }
}

.is-new {
  background: $yellow;

  &:before,
  &:after {
    border-top-color: $yellow;
  }

  &:after {
    border-bottom-color: $yellow;
  }
}

.is-stock {
  background: $btn-color;

  &:before,
  &:after {
    border-top-color: $btn-color;
  }

  &:after {
    border-bottom-color: $btn-color;
  }
}
