.popup-cart-wrap {
  position: absolute;
  top: 0;
  padding: 50px 20px;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, .7);
  z-index: 4;
  overflow-y: auto;
  visibility: hidden;
  opacity: 0;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #bdbdbd;
  }

  @include media(min, 1024) {
    padding-top: 100px;
  }
}

.cart-popup {
  position: relative;
  padding: 37px 20px 30px;
  max-width: 605px;
  width: 100%;
  background: #fff;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: #8fcf00;
    width: 100%;
    height: 22px;
  }

  button {
    @include font-size(0);
    position: absolute;
    top: -35px;
    right: 0;
    padding: 0;
    width: 35px;
    height: 35px;
    border: 0;
    background: transparent;

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 10px;
      left: 5px;
      width: 100%;
      height:1px;
      background: #fff;
    }

    &:before {
      transform: rotate(45deg);
    }

    &:after {
      transform: rotate(-45deg);
    }
  }

  input[type="text"] {
    @include font-size(20);
    font-weight: $light;
    margin-bottom: 15px;
    padding: 10px;
    width: 100%;
    border: 1px solid $light-txt;
  }

  input[type="submit"] {
    display: block;
    margin-top: 15px;
    margin-left: auto;
    padding: 15px;
    border: 0;
    border-radius: 5px;
    background: $main-green;
    color: $bg-txt;
    text-transform: uppercase;
  }

  @include media(min, 500) {
    margin: 0 auto;
  }

  @include media(min, 768) {
    button {
      right: -25px;
      top: -23px;
    }
  }

  @include media(min, 1024) {
    padding: 57px 40px 30px;

    input[type="text"] {
      padding: 20px;
    }
  }
}

.form-title {
  @include font-size(30);
  margin-bottom: 15px;
  text-align: center;
}

.choosen-good-item {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid $light-txt;

  &:last-child {
    margin-bottom: 0;
  }

  img {
    width: 150px;
    margin: 0 auto 10px;
  }

  p {
    @include font-size(16);
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .choosen-good-price {
    @include font-size(24);
    color: $txt-green;
  }

  @include media(min, 500) {
    display: flex;

    img {
      margin-right: 20px;
      margin-bottom: 0;
    }
  }
}
