footer {
  color: $bg-txt;
}

// Footer top
.footer-top {
  background: $footer-top-bg;

  ul {
    @include font-size(14);
    font-weight: $light;
    padding: 20px 0;

    li {
      margin-bottom: 15px;
    }
  }

  @include media(min, 768) {
    ul {
      display: flex;
      justify-content: space-between;

      li {
        margin-bottom: 0;
        margin-right: 15px;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  @include media(min, 1024) {
    ul {
      padding-top: 15px;
      padding-bottom: 15px;

      li {
        display: flex;
        align-items: center;

        img {
          margin-right: 25px;
        }
      }

      p {
        line-height: 1.5;
      }
    }

    .desk-br,
    .desk-icon {
      display: block;
    }
  }

  @include media(min, 1600) {
    ul {
      padding-left: 30px;
      padding-right: 30px;

      li {
        position: relative;

        &:before {
          content: '';
          position: absolute;
          width: 2px;
          height: 65px;
          background: $footer-bottom-bg;
          left: -25px;
        }

        &:first-child:before {
          content: none;
        }
      }
    }
  }
}

.top-list-title {
  @include font-size(18);
  font-weight: $medium;
  margin-bottom: 3px;
  text-transform: uppercase;

  @include media(min, 1024) {
    margin-bottom: 5px;
  }
}

.desk-br,
.desk-icon {
  display: none;
}

// Footer bottom
.footer-bottom {
  @include font-size(18);
  font-weight: $light;
  padding: 20px 0;
  background: $footer-bottom-bg ;

  ul {
    a {
      @include font-size(22);
      font-weight: $regular;
    }

    p {
      @include font-size(14);
    }
  }

  @include media(min, 768) {
    .container {
      display: flex;
    }
  }

  @include media(min, 1024) {
    padding-top: 45px;
    padding-bottom: 45px;
  }

  @include media(min, 1224) {
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      bottom: -100px;
      right: -80px;
      width: 200px;
      height: 200px;
      border: 1px solid;
      border-radius: 50%;
    }
  }

  @include media(min, 1600) {
    &:before {
      content: url("../img/footer-left-img.png");
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}

.bottom-subitem,
.footer-bottom-item {
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }
}

.footer-bottom-item {
  @include media(min, 500) {
    display: flex;

    .bottom-subitem {
      margin-bottom: 0;
      margin-right: 20px;
      width: 50%;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @include media(min, 768) {
    margin-bottom: 0;
    margin-right: 20px;
    width: 50%;

    &:last-child {
      margin-right: 0;
    }
  }
}

.subitem-title {
  @include font-size(22);
  font-weight: $regular;
  margin-bottom: 15px;
}

.footer-bottom-social {
  display: flex;
  margin-bottom: 15px;

  a {
    display: block;
    margin-right: 14px;
    width: 44px;
    height: 44px;
    border: 1px solid;
    background-image: url("../img/footer-social.png");

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      box-shadow: 0 0 3px rgba(0, 0, 0, .3);
    }
  }
}

.vk {
  background-position-x: 2%;
}

.facebook {
  background-position-x: 50%;
}

.ok {
  background-position-x: 98%;
}

.privacy {
  @include font-size(12);
  line-height: 1.3;

  a {
    text-decoration: underline;
  }
}
