// Set box sizing
*,
::after,
::before {
  box-sizing: border-box;
}

// Base styles
html,
body {
  font-size: $mob-font+px;

  @include media(min, 1224) {
    font-size: $desc-width-font+px;
  }
}

body {
  font-family: $roboto;
  min-height: 100vh;
  min-width: 320px;
  color: $dark-txt;
}

a,
button {
  outline: none;

  &:hover {
    cursor: pointer;
  }
}

a {
  text-decoration: none;
  transition: .2s ease;
  color: inherit;

  &:hover {
    text-decoration: underline;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;

  &:last-child {
    margin-bottom: 0;
  }
}

//Images style
img {
  max-width: 100%;
  height: auto;
  display: inherit;
}

// Lists style
ul, ol, dl {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

// Page inner container
.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}

// General style
section {
  margin: 70px 0;
}

.no-scroll {
  overflow: hidden;
}


// Slick plugin style
/* Slider */
.slick-slider {
  position: relative;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;

  .slick-track,
  .slick-list {
    transform: translate3d(0, 0, 0);
  }
}

.slick-list {
  overflow: hidden;

  &:focus {
    outline: none;
  }
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  margin-left: auto;
  margin-right: auto;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }
}
.slick-arrow.slick-hidden {
  display: none;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  bottom: 10px;
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 1;

  &:hover,
  &:focus {
    outline: none;
    background: transparent;
    color: transparent;

    &:before {
      opacity: 1;
    }
  }

  &.slick-disabled:before {
    opacity: .25;
  }

  &:before {
    font-family: "slick";
    font-size: 30px;
    line-height: 1;
    color: $bg-txt;
  }
}

.slick-prev {
  left: 20px;

  &:before {
    content: "\2190";
  }
}

.slick-next {
  right: 30px;

  &:before {
    content: "\2192";
  }
}
